// import { getApps } from './apps';
import { api } from './api';

export class User {

    constructor(code = kintone.getLoginUser().code) {
        this.code = code;
        // this.userRecord = null;
    }

    async getUser() {

        if (!this.user) {
            let { users } = await kintone.api('/v1/users', 'GET', { codes: [this.code] });
            if (users.length > 0) {
                this.user = users[0];
            } else {
                this.user = {};
            }
        }

        return this.user;

    }

    async getGroups() {

        if (!this.groups) {
            let { groups } = await kintone.api('/v1/user/groups', 'GET', { code: this.code });
            this.groups = groups;
        }

        return this.groups;

    }

    async getOrganizations() {

        if (!this.organizations) {
            let { organizationTitles } = await kintone.api('/v1/user/organizations', 'GET', { code: this.code });
            this.organizations = organizationTitles.map(({ organization }) => organization);
        }

        return this.organizations;

    }

    async isInGroups([...targets]) {
        let groups = await this.getGroups();
        return targets.some((target) => groups.some((group) => group.code === target));
    }

    async isInOrganizations([...targets]) {
        let organizations = await this.getOrganizations();
        return targets.some((target) => organizations.some((org) => org.code === target));
    }

    async isIn([...targets]) {
        return this.isInGroups(targets) || this.isInOrganizations(targets);
    }

    // async getUserRecord() {

    //     if (!this.userRecord) {

    //         const { APP_USER_MASTER } = await getApps();

    //         let { records } = await api('/k/v1/records', 'GET', {
    //             app: APP_USER_MASTER.id,
    //             query: `user_code = "${this.code}"`
    //         }, { apiToken: APP_USER_MASTER.token.value });

    //         if (records.length > 0) {
    //             this.userRecord = records[0];
    //         }

    //     }

    //     return this.userRecord;

    // }

    // async field(code) {

    //     let userRecord = await this.getUserRecord();

    //     if (!userRecord[code]) {
    //         return null;
    //     }

    //     return userRecord[code].value;

    // }

    isLoginUser() {
        return this.code === kintone.getLoginUser().code;
    }

    static async getGroupUsers(groupCode) {

        const { users } = await kintone.api('/v1/group/users', 'GET', { code: groupCode });

        if (!users) {
            return [];
        }

        const groupUsers = [];

        users.forEach((user) => {
            groupUsers.push(new User(user.code));
        });

        return groupUsers;

    }

    static async getOrganizationUsers(organizationCode) {

        const { userTitles } = await kintone.api('/v1/organization/users', 'GET', { code: organizationCode });

        if (!userTitles) {
            return [];
        }

        const organizationUsers = [];

        userTitles.forEach(({ user, title }) => {
            organizationUsers.push(new User(user.code));
        });

        return organizationUsers;

    }

}



