import { schema } from 'karin/src/js/schema';
import checkboxHtml from '../html/checkbox.html';

const CHECKBOX_FIELD_ID = schema.fields['checkbox'].id;
const { CS_ORDER_VIEW_ID, CS_ORDER_POSSIBLE_DATE } = ENV;
const TARGET_DATE = new Date(CS_ORDER_POSSIBLE_DATE);

kintone.events.on('app.record.index.show', (event) => {

    $('#csRequest').remove();

    if (event.viewId !== Number(CS_ORDER_VIEW_ID)) {
        return event;
    }

    if (event.records.length === 0) {
        return event;
    }

    $('.recordlist-gaia').addClass('disable-edit').addClass('disable-remove');

    $(`<button id="csRequest" class="btn btn-primary"></button>`).on('click', async function () {

        let recordIds = [];

        $('.recordlist-row-gaia').each((index, row) => {

            let record = event.records[index];

            if ((new Date(record['free_register_time'].value).getTime() - TARGET_DATE.getTime()) < 0) {
                return;
            }

            let isChecked = $(row).find('.input-checkbox-cybozu').prop('checked');
            if (isChecked) {
                recordIds.push(record['$id'].value);
            }

        });

        if (recordIds.length === 0) {
            return;
        }

        let isConfirmed = await karinui.confirm(`${recordIds.length}\n`);

        if (!isConfirmed) {
            return;
        }

        karinui.progress.open();

        try {

            await kintoneUtility.rest.putAllRecords({
                app: kintone.app.getId(),
                records: recordIds.map((recordId) => ({
                    id: recordId,
                    record: {
                        for_customer_support: { value: ['CS'] },
                        is_cs_request: { value: '' }
                    }
                }))
            });

            karinui.progress.close();

            await karinui.alert('');

            location.reload();

        } catch (e) {

            console.log(e);
            karinui.progress.close();
            karinui.alert('');

        }

    }).appendTo(kintone.app.getHeaderMenuSpaceElement());

    $('.recordlist-row-gaia').each((index, row) => {

        let record = event.records[index];

        let $check = $(checkboxHtml);

        $check.on('click', function (e) {

            let $this = $(this);

            if ($this.hasClass('disabled')) {
                return;
            }

            $this.find('input').one('click', (e) => {
                e.stopPropagation();
            }).trigger('click').trigger('focus');

        });

        $check.find('input').on('change', function (e) {

            let $this = $(this);
            let isChecked = $this.prop('checked');

            if (isChecked) {
                $this.closest('.recordlist-row-gaia').addClass('selected-row');
            } else {
                $this.closest('.recordlist-row-gaia').removeClass('selected-row');
            }

        });

        if (!record['free_register_time'].value
            || (new Date(record['free_register_time'].value).getTime() - TARGET_DATE.getTime()) < 0) {
            $check.addClass('disabled');
        }

        $(row).find(`.value-${CHECKBOX_FIELD_ID}`)
            .removeClass('recordlist-decimal-gaia')
            .find('>div').empty().append($check);

    });

    return event;

});



