import { User } from 'karin/src/js/User';
import { schema } from 'karin/src/js/schema';
import { Query } from 'karin/src/js/Query';
import { setAutoNumberField } from 'karin-plugin/src/js/autoNumber';
import { getApps } from 'karin-plugin/src/js/apps';

import link1Html from '../html/link1.html';
import link2Html from '../html/link2.html';
import link3Html from '../html/link3.html';

import './color_code';
import './cs_request';

const APP_ID = kintone.app.getId();
const { CALLING_FROM_SASUKE_ACTION_ID, NEGOTIATION_ACTION_ID } = ENV;

setAutoNumberField('sasuke_id');

/*eslint no-extend-native: "off"*/
Array.prototype.filterX = function (callback) {

    let array = [];
    let len = this.length;

    for (let i = 0; i < len; i++) {
        let result = callback(this[i], i);
        if (result === true) {
            array.push(this[i]);
            this.splice(i, 1);
            i -= 1;
            len -= 1;
        }
    }

    return array;

};

