kintone.events.on('app.record.index.show', (event) => {

    $('#bulkCalling').remove();

    (async function () {

        const IS_MANAGER = await new User().isInGroups(['GR_MANAGEMENT']);

        if (!IS_MANAGER) {
            return;
        }

        $('<button id="bulkCalling" class="btn btn-danger"></button>')
            .on('click', async function () {

                const DAYS = 31;

                try {

                    const { APP_CALLING_MANAGER } = await getApps();
                    const isConfirmed = await karinui.confirm('\n');

                    if (!isConfirmed) {
                        return;
                    }

                    karinui.progress.open();

                    let param = { app: APP_ID, records: [] };

                    let leadResp = await kintoneUtility.rest.getAllRecordsByQuery({
                        app: APP_ID,
                        query: 'updated_time > FROM_TODAY(-1, MONTHS)'
                    });

                    let callingResp = await kintoneUtility.rest.getAllRecordsByQuery({
                        app: APP_CALLING_MANAGER.id,
                        query: 'updated_time > FROM_TODAY(-1, MONTHS)'
                    });

                    let leadRecords = leadResp.records;
                    let callingRecords = callingResp.records;

                    leadRecords
                        .filter((leadRecord) => Boolean(leadRecord['sasuke_id'].value))
                        .forEach((leadRecord) => {

                            let sasukeId = leadRecord['sasuke_id'].value;
                            let customerId = leadRecord['customer_id'].value;

                            let common = callingRecords
                                .filter((callingRecord) => {

                                    if (callingRecord['sasuke_id'].value) {
                                        return Number(sasukeId) === Number(callingRecord['sasuke_id'].value);
                                    } else if (callingRecord['customer_id'].value) {
                                        return Number(customerId) === Number(callingRecord['customer_id'].value);
                                    }

                                    return false;

                                })
                                .sort((a, b) => new Date(b['created_time'].value)
                                    - new Date(a['created_time'].value))[0];

                            let front = callingRecords
                                .filter((record) => record['type'].value === '')
                                .filter((callingRecord) => {

                                    if (callingRecord['sasuke_id'].value) {
                                        return Number(sasukeId) === Number(callingRecord['sasuke_id'].value);
                                    } else if (callingRecord['customer_id'].value) {
                                        return Number(customerId) === Number(callingRecord['customer_id'].value);
                                    }

                                    return false;

                                })
                                .sort((a, b) => new Date(b['created_time'].value)
                                    - new Date(a['created_time'].value))[0];

                            let cs = callingRecords
                                .filter((record) => record['type'].value === '')
                                .filter((callingRecord) => {

                                    if (callingRecord['sasuke_id'].value) {
                                        return Number(sasukeId) === Number(callingRecord['sasuke_id'].value);
                                    } else if (callingRecord['customer_id'].value) {
                                        return Number(customerId) === Number(callingRecord['customer_id'].value);
                                    }

                                    return false;

                                })
                                .sort((a, b) => new Date(b['created_time'].value)
                                    - new Date(a['created_time'].value))[0];

                            let updateRecord = {
                                id: leadRecord['$id'].value,
                                record: {
                                    sasuke_id: { value: sasukeId }
                                }
                            };

                            if (common || front || cs) {

                                if (common) {
                                    updateRecord.record['common_latest_calling_id'] = { value: common['$id'].value };
                                }

                                if (front) {
                                    updateRecord.record['latest_calling_id'] = { value: front['$id'].value };
                                }

                                if (cs) {
                                    updateRecord.record['cs_latest_calling_id'] = { value: cs['$id'].value };
                                }

                                param.records.push(updateRecord);

                            }

                        });

                    await kintoneUtility.rest.putAllRecords(param);

                    karinui.progress.close();
                    karinui.alert('');

                } catch (e) {
                    console.log(e);
                    karinui.progress.close();
                    karinui.alert('');
                }

            }).appendTo(kintone.app.getHeaderMenuSpaceElement());

    })();

    return event;

});

kintone.events.on(['app.record.edit.show'], (event) => {

    event.record['scan_status'].disabled = true;

    return event;

});

const CUSTOMER_SUPPORT_VIEW = 'CS';

kintone.events.on('app.record.index.show', (event) => {

    if (event.records.length === 0) {
        return event;
    }

    let $rows = $('.recordlist-row-gaia');

    (async function () {

        const { APP_CALLING_MANAGER, APP_CUSTOMER_MASTER, APP_NEGOTIATION_MANAGER } = await getApps();

        for (let i = 0; i < $rows.length; i++) {

            let row = $rows[i];
            let record = event.records[i];

            let $link1 = $(link1Html);
            let $link2 = $(link2Html);
            let $link3 = $(link3Html);

            let fieldId1 = schema.fields['customer_master'].id;
            let fieldId2 = schema.fields['calling_manager'].id;
            let fieldId3 = schema.fields['negotiation_manager'].id;

            let link = `/k/${APP_CALLING_MANAGER.id}/edit`
                + `?action=${CALLING_FROM_SASUKE_ACTION_ID}&app=${APP_ID}&record=${record['$id'].value}`;

            if (event.viewName === CUSTOMER_SUPPORT_VIEW) {
                link += `&calling_type=cs`;
            }

            $link2.attr('href', link);

            if (record['customer_record_id'].value) {

                let recordId = record['customer_record_id'].value;

                $link1.attr('href', `/k/${APP_CUSTOMER_MASTER.id}/show#record=${recordId}`);
                $link3.attr('href', `/k/${APP_NEGOTIATION_MANAGER.id}/edit`
                    + `?action=${NEGOTIATION_ACTION_ID}&app=${APP_CUSTOMER_MASTER.id}&record=${recordId}`);

            } else {
                $link1.addClass('disabled');
                $link3.addClass('disabled');
            }

            $(row).find(`.value-${fieldId1}`)
                .removeClass('recordlist-decimal-gaia')
                .find('>div').empty().append($link1);

            $(row).find(`.value-${fieldId2}`)
                .removeClass('recordlist-decimal-gaia')
                .find('>div').empty().append($link2);

            $(row).find(`.value-${fieldId3}`)
                .removeClass('recordlist-decimal-gaia')
                .find('>div').empty().append($link3);

        }

    })();

    return event;

});

kintone.events.on(['app.record.detail.show'], (event) => {

    $('#customer_link').remove();

    let recordId = event.record['customer_record_id'].value;

    if (!recordId) {
        return event;
    }

    (async function () {

        const { APP_CUSTOMER_MASTER } = await getApps();

        $('<a id="customer_link" class="btn btn-primary" target="_blank"></a>')
            .attr('href', `/k/${APP_CUSTOMER_MASTER.id}/show#record=${recordId}&mode=edit`)
            .appendTo(kintone.app.record.getSpaceElement('space_link'));

    })();

    return event;

});

kintone.events.on([
    'app.record.create.submit'
], async (event) => {

    let companyName = event.record['company_name'].value;
    let customerName = event.record['customer_name'].value;

    let query = new Query()
        .param('company_name', '=', companyName)
        .param('customer_name', '=', customerName);

    if (event.type === 'app.record.edit.submit') {
        query.param('$id', '!=', event.record['$id'].value);
    }

    let { records } = await kintone.api('/k/v1/records', 'GET', {
        app: kintone.app.getId(), query: query.query()
    });

    if (records.length > 0) {
        await karinui.alert('');
        window.open(`/k/${kintone.app.getId()}/show#record=${records[0]['$id'].value}`);
        event.error = '';
    }

    return event;

});



