import { Query, QueryFn, QueryConst, LOG_OPS, REL_OPS, SORT_TYPES } from '.';

const RE_OPTIONS = /(order by (.+?)|)\s*(limit (.+?)|)\s*(offset (.+?)|)\s*$/;

function isNull(n) {
    return n === null || n === undefined;
}

export function parse(queryString) {

    const quoteStorage = [];

    function replaceQuote(str) {

        let array = str.split(''),
            len = array.length,
            current = null;

        let result = '';

        for (let i = 0; i < len; i++) {

            let s = array[i],
                prev = array[i - 1];

            let isEsc = prev === '\\';

            if (current) {
                current.str += s;
            }

            if (!isEsc && s === '"') {

                if (current) {

                    current.endAt = i;

                    let index = quoteStorage.length;
                    result += `%%quote${index}%%`;
                    quoteStorage[index] = current.str;

                    current = null;

                } else {
                    current = { startAt: i, str: s };
                }

            } else if (!current) {
                result += s;
            }

        }

        return result;

    }

    function restoreQuote(str) {
        return str.replace(/%%quote(\d+)%%/g, (str, p1) => quoteStorage[p1]);
    }

    const parenthStorage = [];

    function replaceParenth(str) {

        let array = str.split(''),
            len = array.length,
            current = null;

        let result = '';

        for (let i = 0; i < len; i++) {

            let s = array[i],
                prev = array[i - 1];

            let isEsc = prev === '\\';

            let c = current;

            while (c) {
                c.str += s;
                c = c.prev;
            }

            if (!isEsc && s === '(') {

                current = { startAt: i, str: s, prev: current };

            } else if (!isEsc && s === ')') {

                if (current) {

                    current.endAt = i;

                    if (!current.prev) {
                        let index = parenthStorage.length;
                        result += `%%parenth${index}%%`;
                        parenthStorage[index] = current.str;
                    }

                    current = current.prev;

                }

            } else if (!current) {
                result += s;
            }

        }

        return result;

    }

    function restoreParenth(str) {
        return str.replace(/%%parenth(\d+)%%/g, (str, p1) => parenthStorage[p1]);
    }

    function isParenth(str) {
        return /^%%parenth(\d+)%%$/.test(str);
    }

    function parseValue(_str) {

        let str = _str;
        str = str.trim();

        if (isNull(str)) {
            return '';
        }

        if (/^\(.*\)$/.test(str)) {

            str = str.replace(/(^\(|\)$)/g, '');
            str = replaceQuote(str);
            str = replaceParenth(str);

            return str.split(',')
                .filter((s) => s)
                .map((s) => s.trim())
                .map((s) => restoreParenth(s))
                .map((s) => restoreQuote(s))
                .map((s) => parseValue(s));

        }

        if (/^".*"$/.test(str)) {
            return str.replace(/(^"|"$)/g, '');
        }

        if (!isNaN(Number(str))) {
            return Number(str);
        }

        const RE_QUERY_FN = /^(.+?)(\(.*\))$/;

        if (RE_QUERY_FN.test(str)) {
            let { 1: fnName, 2: args } = str.match(RE_QUERY_FN);
            return new QueryFn(fnName, ...parseValue(args));
        }

        return new QueryConst(str);

    }

    function parseQuery(_queryString) {

        let queryString = _queryString;

        queryString = queryString.trim();
        queryString = replaceQuote(queryString);
        queryString = replaceParenth(queryString);

        queryString = queryString.replace(RE_OPTIONS, '');
        queryString = queryString.trim();

        let split = queryString.split(' ');

        let params = [], logOps = [];
        let field = null, relOp = null, value = null;

        for (let i = 0; i < split.length; i++) {

            let str = split[i], next = split[i + 1];

            let parenth = isParenth(str);

            str = restoreParenth(str);
            str = restoreQuote(str);

            if (field === null) {

                if (parenth) {

                    let queryString = str.replace(/(^\(|\)$)/g, '');
                    params.push({ type: 'query', query: parseQuery(queryString) });

                } else {
                    field = str;
                }

            } else if (relOp === null) {

                if (str === 'not') {
                    relOp = [str, next].join(' ');
                    i++;
                } else {
                    relOp = str;
                }

                if (REL_OPS.indexOf(relOp) === -1) {
                    throw new Error(`${relOp} is invalid relational operator`);
                }

            } else if (value === null) {

                value = parseValue(str);
                params.push({ type: 'param', field, relOp, value });

            } else {

                let logOp = str;

                if (LOG_OPS.indexOf(logOp) === -1) {
                    throw new Error(`${logOp} is invalid logical operator`);
                }

                logOps.push(logOp);

                field = null;
                relOp = null;
                value = null;

            }

        }

        let logOp = logOps.indexOf('or') > -1 ? 'or' : 'and';

        let mainQuery = new Query(logOp);
        let currentQuery = null;

        params.forEach(({ type, query, field, relOp, value }, i) => {

            let logOpBefore = logOps[i - 1] || logOp,
                logOpAfter = logOps[i] || logOp;

            let currentOperator = logOpBefore === logOpAfter ? logOpBefore : 'and';

            if (currentOperator === logOp) {

                currentQuery = null;

                switch (type) {
                    case 'param':
                        mainQuery.param(field, relOp, value);
                        break;
                    case 'query':
                        mainQuery.join(query);
                        break;
                }

            } else {

                if (!currentQuery) {
                    currentQuery = new Query();
                    mainQuery.join(currentQuery);
                }

                switch (type) {
                    case 'param':
                        mainQuery.param(field, relOp, value);
                        break;
                    case 'query':
                        mainQuery.join(query);
                        break;
                }

            }

        });

        return mainQuery;

    }

    function parseOptions(_queryString) {

        let queryString = _queryString;

        queryString = queryString.trim();

        let match = queryString.match(RE_OPTIONS) || [];

        let orders = (match[2] || '').split(',')
            .filter((n) => n)
            .map((str) => str.trim().split(' '))
            .map(([field, sortType]) => ({ field, sortType }));

        let limit = isNull(match[4]) ? null : Number(match[4]);
        let offset = isNull(match[6]) ? null : Number(match[6]);

        orders.forEach(({ sortType }) => {
            if (SORT_TYPES.indexOf(sortType) === -1) {
                throw new Error(`${sortType} is invalid sort type`);
            }
        });

        if (!isNull(limit) && isNaN(limit)) {
            throw new Error('limit must be a number');
        }

        if (!isNull(offset) && isNaN(offset)) {
            throw new Error('offset must be a number');
        }

        return { orders, limit, offset };

    }

    let query = parseQuery(queryString);
    let { orders, limit, offset } = parseOptions(queryString);

    if (orders.length > 0) {
        orders.forEach(({ field, sortType }) => {
            query.orderBy(field, sortType);
        });
    }

    if (!isNull(limit)) {
        query.limit(limit);
    }

    if (!isNull(offset)) {
        query.offset(offset);
    }

    return query;

}



