// import Promise from './Promise';

const MAX_API = 1000;

let apiCnt = 0;

export function api(api, method, param, options = {}) {

    if (apiCnt++ > MAX_API) {
        throw new Error('Exceeded Max Api Request Count');
    }

    let {
        isGuest,
        domain,
        guestSpaceId,
        basicAuth,
        userAuth,
        apiToken
    } = options;

    let basicAuthBase64;
    if (basicAuth) {
        basicAuthBase64 = btoa(basicAuth);
    }

    let userAuthBase64;
    if (userAuth) {
        userAuthBase64 = btoa(userAuth);
    }

    let makeRequestURL = () => {

        // method = encodeURIComponent(method);

        let hostname = (domain) ? domain : location.hostname;
        if (!hostname) {
            return false;
        }

        if (guestSpaceId) {
            let urlParts = api.split('/');
            return `https://${hostname}/k/guest/${guestSpaceId}/`
                + `${urlParts[2]}/${urlParts[3]}.json?_method=${method}`;
        } else if (!isGuest) {
            return `https://${hostname}${api}.json?_method=${method}`;
        } else if (window && window.kintone && isGuest) {
            return kintone.api.url(api, isGuest);
        }

    };

    let setOverrideHeader = (xhr) => {
        xhr.setRequestHeader('X-HTTP-Method-Override', method);
    };

    let setBasicAuthHeader = (xhr) => {
        if (basicAuthBase64) {
            xhr.setRequestHeader('Authorization', `Basic ${basicAuthBase64}`);
        }
    };

    let setUserAuthHeader = (xhr) => {
        if (userAuthBase64) {
            xhr.setRequestHeader('X-Cybozu-Authorization', userAuthBase64);
            return true;
        }
        return false;
    };

    let setApiTokenAuthHeader = (xhr) => {
        if (apiToken) {
            xhr.setRequestHeader('X-Cybozu-API-Token', apiToken);
            return true;
        }
        return false;
    };

    let setRequestToken = () => {
        if (window && window.kintone) {
            param['__REQUEST_TOKEN__'] = kintone.getRequestToken();
        }
    };

    let setHeaders = (xhr) => {

        if (!(api.indexOf('file') > -1) || method !== 'POST') {
            xhr.setRequestHeader('Content-Type', 'application/json');
        }

        xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');

        setOverrideHeader(xhr);
        setBasicAuthHeader(xhr);

        let isUserAuth = setUserAuthHeader(xhr);
        let isApiTokenAuth = setApiTokenAuthHeader(xhr);

        if (!isUserAuth && !isApiTokenAuth) {
            return true;
        }

        return false;

    };

    let send = (xhr, resolve, reject) => {

        xhr.onload = function (r) {
            if (xhr.status === 200) {
                resolve(JSON.parse(xhr.responseText));
            } else {
                reject(JSON.parse(xhr.responseText));
            }
        };

        xhr.send(JSON.stringify(param));

    };

    let getFile = (xhr, resolve, reject) => {

        xhr.responseType = 'blob';

        xhr.onload = function (r) {
            if (xhr.status === 200) {
                resolve(xhr.response);
            } else {
                reject(xhr.statusText);
            }
        };

        xhr.send(JSON.stringify(param));

    };

    let postFile = (xhr, resolve, reject) => {

        let formData = new FormData();

        if (param['__REQUEST_TOKEN__']) {
            formData.append('__REQUEST_TOKEN__', param['__REQUEST_TOKEN__']);
        }

        formData.append('file', param.blob, param.fileName);

        xhr.onload = function (r) {
            if (xhr.status === 200) {
                resolve(JSON.parse(xhr.responseText));
            } else {
                reject(JSON.parse(xhr.responseText));
            }
        };

        xhr.send(formData);

    };

    return new Promise((resolve, reject) => {

        let xhr = new XMLHttpRequest();
        let url = makeRequestURL();

        if (!url) {
            reject();
        } else {

            xhr.open('POST', url, true);

            let isNeededCSRFToken = setHeaders(xhr);
            if (isNeededCSRFToken) {
                setRequestToken();
            }

            if (!(api.indexOf('file') > -1)) {
                // record
                send(xhr, resolve, reject);
            } else if (method === 'GET') {
                // file
                getFile(xhr, resolve, reject);
            } else {
                postFile(xhr, resolve, reject);
            }

        }

    });

}

export const downloadFile = api.downloadFile = ({ fileKey }, options) => {
    return api('/k/v1/file', 'GET', { fileKey }, options);
};

export const uploadFile = api.uploadFile = ({ fileName, blob }, options) => {
    return api('/k/v1/file', 'POST', { fileName, blob }, options);
};

export const resetCnt = api.resetCnt = () => {
    apiCnt = 0;
};



