import { Query } from 'karin/src/js/Query';
import { getApps } from 'karin-plugin/src/js/apps';

import alertHtml from '../html/alert.html';

let companyRecords = [];
let projectRecords = [];
// let customerRecords = [];
let callingRecords = [];
let negotiationRecords = [];
let frontUsers = [];

async function getFrontUsers() {

    return Promise.all([
        kintone.api('/v1/organization/users', 'GET', { code: 'ORG_MRKT' })
    ]).then((orgs) => {

        frontUsers = [];

        orgs.forEach(({ userTitles }) => {
            userTitles.forEach(({ user }) => {
                frontUsers.push(user.code);
            });
        });

    });

}

// 
async function getCompanyRecords(companyIds) {

    const { APP_COMPANY_MASTER } = await getApps();

    let query = new Query().param('company_id', 'in', companyIds).query();

    return kintoneUtility.rest.getAllRecordsByQuery({
        app: APP_COMPANY_MASTER.id, query,
        fields: ['company_id', 'key_enterprise']
    }).then(({ records }) => {
        companyRecords = records;
    });

}

// S
async function getProjectRecords(companyIds) {

    const { APP_PROJECT_MANAGEMENT } = await getApps();

    let query = new Query()
        .param('company_id', 'in', companyIds)
        .param('accuracy', 'in', [''])
        .param('project_class', 'in', [''])
        .param('yomi_date', '>', '2015-06-01')
        .param('front', 'not in', ['Administrator'])
        .query();

    return kintoneUtility.rest.getAllRecordsByQuery({
        app: APP_PROJECT_MANAGEMENT.id, query,
        fields: ['company_id']
    }).then(({ records }) => {
        projectRecords = records;
    });

}

async function getCustomerIds(companyIds) {

    const { APP_CUSTOMER_MASTER } = await getApps();

    let query = new Query().param('company_id', 'in', companyIds).query();

    return kintoneUtility.rest.getAllRecordsByQuery({
        app: APP_CUSTOMER_MASTER.id, query, fields: ['customer_id']
    }).then(({ records }) => {
        return records.map((record) => record['customer_id'].value).filter(n => n);
    });

}

async function getCallingRecords(customerIds) {

    const { APP_CALLING_MANAGER } = await getApps();

    let query = new Query()
        .param('customer_id', 'in', customerIds)
        .param('time', '>=', Query.fn('FROM_TODAY', -4, Query.const('MONTHS')))
        .param('result', 'in', ['_', '1_', 'S_', 'CS_'])
        // .param('user', 'in', frontUsers)
        .query();

    return kintoneUtility.rest.getAllRecordsByQuery({
        app: APP_CALLING_MANAGER.id, query,
        fields: ['company_id', 'customer_id']
    }).then(({ records }) => {
        callingRecords = records;
    });

}

async function getNegotiationRecords(customerIds) {

    const { APP_NEGOTIATION_MANAGER } = await getApps();

    let query = new Query()
        .param('customer_id', 'in', customerIds)
        .param('negotiation_date', '>=', Query.fn('FROM_TODAY', -4, Query.const('MONTHS')))
        // .param('front', 'in', frontUsers)
        .query();

    return kintoneUtility.rest.getAllRecordsByQuery({
        app: APP_NEGOTIATION_MANAGER.id, query,
        fields: ['company_id', 'customer_id']
    }).then(({ records }) => {
        negotiationRecords = records;
    });

}

async function prepareRecords(companyIds) {

    return Promise.all([
        // getFrontUsers(companyIds),
        getCompanyRecords(companyIds),
        getProjectRecords(companyIds)
    ])
        .then(() => getCustomerIds(companyIds))
        .then((customerIds) => Promise.all([
            getCallingRecords(customerIds),
            getNegotiationRecords(customerIds)
        ]));

}

function isPriorityCompany(record) {

    let companyId = Number(record['company_id'].value);

    if (!companyId) {
        return false;
    }

    let companyRecord = companyRecords.find((r) => Number(r['company_id'].value) === companyId);

    if (!companyRecord) {
        return false;
    }

    let keyEnterprise = companyRecord['key_enterprise'].value;
    return (keyEnterprise.includes('') || keyEnterprise.includes(''));

}

function isGenS(record) {

    let companyId = Number(record['company_id'].value);

    if (!companyId) {
        return false;
    }

    let projectRecord = projectRecords.find((r) => Number(r['company_id'].value) === companyId);

    if (!projectRecord) {
        return false;
    }

    return true;

}

function isFourMonth(record) {

    let companyId = Number(record['company_id'].value);

    if (!companyId) {
        return false;
    }

    let callingRecord = callingRecords.find((r) => Number(r['company_id'].value) === companyId);
    let negotiationRecord = negotiationRecords.find((r) => Number(r['company_id'].value) === companyId);

    if (!callingRecord && !negotiationRecord) {
        return false;
    }

    return true;

}

kintone.events.on('app.record.index.show', (event) => {

    // if (event.viewId === Number(CS_ORDER_VIEW_ID)) {
    //     return event;
    // }

    if (event.records.length > 0) {

        let companyIds = event.records
            .map((record) => Number(record['company_id'].value))
            .filter((n) => Boolean(n));

        prepareRecords(companyIds).then(() => {

            $('.recordlist-row-gaia').each((index, row) => {

                let record = event.records[index];
                let $row = $(row);

                if (isPriorityCompany(record)) {
                    $row.addClass('row-priority-company');
                } else if (isGenS(record)) {
                    $row.addClass('row-gen-s');
                } else if (isFourMonth(record)) {
                    $row.addClass('row-four-month');
                }

            });

        });

    }

    return event;

});

kintone.events.on('app.record.detail.show', (event) => {

    let companyIds = [Number(event.record['company_id'].value)];

    prepareRecords(companyIds).then(() => {

        let headerMenu = kintone.app.record.getHeaderMenuSpaceElement();

        let $alert;
        if (isPriorityCompany(event.record)) {

            $alert = $(alertHtml).appendTo(headerMenu);
            $alert.addClass('alert-priority-company')
                .find('span').text(' ');

            $('.gaia-argoui-app-show-toolbar').addClass('toolbar-priority-company');

        } else if (isGenS(event.record)) {

            $alert = $(alertHtml).appendTo(headerMenu);
            $alert.addClass('alert-gen-s')
                .find('span').text(' S');

            $('.gaia-argoui-app-show-toolbar').addClass('toolbar-gen-s');

        } else if (isFourMonth(event.record)) {

            $alert = $(alertHtml).appendTo(headerMenu);
            $alert.addClass('alert-four-month')
                .find('span').text(' 4');

            $('.gaia-argoui-app-show-toolbar').addClass('toolbar-four-month');

        }

    });

    return event;

});



