import { api } from '../../../karin/src/js/api';

const { appMaster } = karin.environments[location.hostname];

let promise;

export function getApps() {

    if (promise) {
        return promise;
    }

    promise = api('/k/v1/records', 'GET', {
        app: appMaster.id,
        query: 'app_code != ""',
        limit: 500,
        fields: ['app_code', 'app_id', 'app_name', 'api_token', 'api_token_rights']
    }, { apiToken: appMaster.token.value }).then(({ records }) => {

        const APPS = {};

        records.forEach((record) => {

            let appCode = record['app_code'].value;

            if (!appCode) {
                return;
            }

            APPS[appCode] = {
                id: record['app_id'].value,
                code: record['app_code'].value,
                name: record['app_name'].value,
                token: {
                    value: record['api_token'].value,
                    rights: {
                        viewRecord: record['api_token_rights'].value.includes(''),
                        addRecord: record['api_token_rights'].value.includes(''),
                        editRecord: record['api_token_rights'].value.includes(''),
                        deleteRecord: record['api_token_rights'].value.includes(''),
                        editApp: record['api_token_rights'].value.includes('')
                    }
                }
            };

        });

        return APPS;

    });

    return promise;

}



