import { api } from '../../../karin/src/js/api';
import { getApps } from './apps';

export async function getAutoNumber(appId, errCnt = 0) {

    if (errCnt > 9) {
        throw new Error('Exceeded Maximum Errors');
    }

    const { APP_AUTO_NUMBER } = await getApps();
    const API_TOKEN = APP_AUTO_NUMBER.token.value;

    try {

        let { records } = await api('/k/v1/records', 'GET', {
            app: APP_AUTO_NUMBER.id,
            query: `app_id = ${appId} limit 1`,
            fields: ['$id', '$revision', 'last_num']
        }, { apiToken: API_TOKEN });

        if (records.length === 0) {

            await api('/k/v1/record', 'POST', {
                app: APP_AUTO_NUMBER.id,
                record: {
                    app_id: { value: appId },
                    last_num: { value: 1 }
                }
            }, { apiToken: API_TOKEN });

            return 1;

        }

        let record = records[0];
        let autoNumber = Number(record['last_num'].value) + 1;

        await api('/k/v1/record', 'PUT', {
            app: APP_AUTO_NUMBER.id,
            id: record['$id'].value,
            revision: record['$revision'].value,
            record: {
                last_num: { value: autoNumber }
            }
        }, { apiToken: API_TOKEN });

        return autoNumber;

    } catch (e) {
        return await getAutoNumber(appId, errCnt + 1);
    }

}

export function setAutoNumberField(fieldCode) {

    kintone.events.on([
        'app.record.create.show',
        'app.record.edit.show',
        'app.record.index.edit.show'
    ], (event) => {
        event.record[fieldCode].disabled = true;
        return event;
    });

    kintone.events.on(['app.record.create.show'], (event) => {

        getAutoNumber(kintone.app.getId()).then((autoNumber) => {
            let rec = kintone.app.record.get();
            rec.record[fieldCode].value = autoNumber;
            kintone.app.record.set(rec);
        }, (err) => {
            alert('');
        });

        return event;

    });

}



