import types from './types.json';

function clone(n) {
    return JSON.parse(JSON.stringify(n));
}

function convFieldType(fieldType) {
    // return Object.keys(types).find((type) => types[type].schemaType === fieldType);
    return Object.keys(types).filter((type) => types[type].schemaType === fieldType)[0];
}

function findById(obj, id) {
    // let code = Object.keys(obj).find((code) => obj[code].id === id);
    let code = Object.keys(obj).filter((code) => obj[code].id === id)[0];
    return obj[code];
}

function getSchema({ table: { fieldList }, subTable, groups } = {}) {

    let schema = { fields: {}, subTables: {}, groups: {}, all: {} };

    if (fieldList) {

        Object.keys(fieldList).forEach((fieldId) => {

            let { id, type, label, properties } = fieldList[fieldId];
            let code = fieldList[fieldId].var;

            if (type === 'GROUP') {
                schema.groups[code] = {
                    id, code, label,
                    fields: []
                };
            } else {
                schema.fields[code] = {
                    id, code, label, properties,
                    type: convFieldType(type),
                    schemaType: type
                };
            }

        });

    }

    if (subTable) {

        Object.keys(subTable).forEach((subTableId) => {

            let { id, label, fieldList } = subTable[subTableId];
            let subTableCode = subTable[subTableId].var, code = subTableCode;

            schema.subTables[code] = { id, code, label, type: 'SUBTABLE', fields: {} };

            Object.keys(fieldList).forEach((fieldId) => {

                let { id, type, label, properties } = fieldList[fieldId];
                let code = fieldList[fieldId].var;

                schema.subTables[subTableCode].fields[code] = {
                    id, code, label, properties,
                    type: convFieldType(type),
                    schemaType: type,
                    subTable: schema.subTables[subTableCode]
                };

            });

        });

    }

    if (groups) {

        groups.forEach(({ childLocalId, vtComponentLocalId }) => {

            let field = findById(schema.fields, childLocalId);
            let group = findById(schema.groups, vtComponentLocalId);

            field.group = group;
            group.fields.push(field);

        });

    }

    Object.keys(schema.fields).forEach((code) => {
        let field = schema.fields[code];
        schema.all[field.code] = field;
    });

    Object.keys(schema.subTables).forEach((subTableCode) => {

        let subTable = schema.subTables[subTableCode];
        schema.all[subTable.code] = subTable;

        Object.keys(subTable.fields).forEach((code) => {
            let field = subTable.fields[code];
            schema.all[field.code] = field;
        });

    });

    return schema;

}

const FORM_DATA = (cybozu && cybozu.data && cybozu.data.page && cybozu.data.page && cybozu.data.page.FORM_DATA)
    ? clone(cybozu.data.page.FORM_DATA) : {};

// layout is in json string
FORM_DATA.layout = JSON.parse(FORM_DATA.layout);

export const schema = getSchema(FORM_DATA.schema);

let rowIndex = 0;

FORM_DATA.layout.forEach((row) => {

    if (row.type === 'ROW') {

        row.controlList.forEach((field, colIndex) => {

            if (field.type === 'GROUP') {
                field.canvasData.forEach((row) => {
                    row.controlList.forEach((field, colIndex) => {
                        if (field.var) {
                            let code = field.var;
                            schema.fields[code].rowIndex = rowIndex;
                            schema.fields[code].colIndex = colIndex;
                        }
                    });
                    rowIndex++;
                });
            } else {
                if (field.var) {
                    let code = field.var;
                    schema.fields[code].rowIndex = rowIndex;
                    schema.fields[code].colIndex = colIndex;
                }
                rowIndex++;
            }

        });

    } else if (row.type === 'SUBTABLE') {

        let subTableCode = row.var;
        let subTable = schema.subTables[subTableCode];
        subTable.rowIndex = rowIndex;

        row.controlList.forEach((field, colIndex) => {
            let code = field.var;
            subTable.fields[code].rowIndex = rowIndex;
            subTable.fields[code].colIndex = colIndex;
        });

        rowIndex++;

    }

});

schema.referenceTables = [];

FORM_DATA.referenceTables.forEach(({
    id, masterApp, fieldId,
    filterInputFieldId, filterMasterFieldId,
    masterFieldIds, query, size
}) => {

    let field = findById(schema.all, fieldId);

    // let referenceTable = {
    //     id, field, masterApp, masterAppSchema,
    //     filterInputField: findById(schema.fields, filterInputFieldId),
    //     filterMasterField: findById(masterAppSchema.fields, filterMasterFieldId),
    //     masterFields: masterFieldIds.map((fieldId) => findById(masterAppSchema.fields, fieldId)),
    //     query: query, size: size
    // };

    let referenceTable = {
        id, field, masterApp,
        filterInputField: findById(schema.all, filterInputFieldId),
        query: query, size: size
    };

    if (masterApp.accessible) {

        let masterAppSchema = getSchema(masterApp.schema);

        referenceTable.masterAppSchema = masterAppSchema;
        referenceTable.filterMasterFieldId = findById(masterAppSchema.all, filterMasterFieldId);
        referenceTable.masterFields = masterFieldIds.map((fieldId) => findById(masterAppSchema.all, fieldId));

    } else {

        referenceTable.masterAppSchema = null;
        referenceTable.filterMasterFieldId = null;
        referenceTable.masterFields = [];

    }

    field.referenceTable = referenceTable;

    schema.referenceTables.push(referenceTable);

});

schema.lookups = [];

FORM_DATA.lookups.forEach(({
    id, keyMapping, targetApp, fieldMappings, listFields, query
}) => {

    let { fieldId, targetFieldId } = keyMapping;
    let field = findById(schema.all, fieldId);

    // let lookup = {
    //     id, targetApp, targetAppSchema,
    //     keyMapping: { field, targetField },
    //     fieldMappings: fieldMappings.map(({ fieldId, targetFieldId }) => ({
    //         field: findById(schema.fields, fieldId),
    //         targetField: findById(targetAppSchema.fields, targetFieldId)
    //     })),
    //     listFields: listFields.map((fieldId) => findById(targetAppSchema.fields, fieldId)),
    //     query
    // };

    let lookup = { id, targetApp, query };

    if (targetApp.accessible) {

        let targetAppSchema = getSchema(targetApp.schema);
        let targetField = findById(targetAppSchema.all, targetFieldId);

        lookup.keyMapping = { field, targetField };
        lookup.targetAppSchema = targetAppSchema;
        lookup.fieldMappings = fieldMappings.map(({ fieldId, targetFieldId }) => ({
            field: findById(schema.all, fieldId),
            targetField: findById(targetAppSchema.all, targetFieldId)
        }));
        lookup.listFields = listFields.map((fieldId) => findById(targetAppSchema.all, fieldId));

    } else {

        lookup.keyMapping = { field, targetField: null };
        lookup.targetAppSchema = null;
        lookup.fieldMappings = fieldMappings.map(({ fieldId, targetFieldId }) => ({
            field: findById(schema.all, fieldId),
            targetField: null
        }));
        lookup.listFields = [];

    }

    field.lookup = lookup;

    schema.lookups.push(lookup);

});



